import React, { useEffect, useState } from 'react';
import { Divider, Grid, Paper, Table, TableBody, TableCell, TableHead, TableContainer, TableRow } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles'
;
import customAxios from "../apis/Api"
import Label from '../../components/Label';
function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
}

const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
];


const useStyle = makeStyles((theme) => ({
    PayRoll: {
        display: "flex", justifyContent: "space-between", marginTop: 10, alignItems: "stretch", flexWrap: "wrap", paddingLeft: 16, lineHeight: 0.5, backgroundColor: "#f1bc00"
    }, root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(1),
        marginTop: 10,
        textAlign: 'start',
        fontWeight: 500,
        color: "#f1f1f1",
        lineHeight: 3,
        // fontSize:1.75,
        color: theme.palette.text.secondary,
    },
    paper2: {
        border: "1 px solid black",
        color: "#919eab",
        marginBottom: 10,
    }, Trow: {

        color: "#919eab",
        textTransform: "uppercase"
    }
}))
const PayRoll = (props) => {
    const { row } = props;
    const [paymentState, setPaymentState] = useState([])
    console.log(row)
    const PaymentFunction = async () => {
        const Data = await customAxios().get(`finance/saleryList/${row.id}`)
        setPaymentState(Data.data)
    }

    useEffect(() => {
        PaymentFunction();
    }, [])
    const classes = useStyle();
    return (
        <>

            <div style={{ paddingLeft: 10, paddingRight: 10, height: "100vh" }} >
                <div className={classes.PayRoll}>

                    <h4>
                        Pay ROll
                    </h4>
                </div>
                <div className={classes.root}>
                    <Grid ontainer spacing={1}>

                        <Grid container item xs={12} spacing={3}>
                            <Grid item xs={4}>
                                <Paper className={classes.paper}> {`EPF No :  ${row.epf_No}`} </Paper>

                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.paper}> {`Basic Salary :  ${row.basic_salery}`}</Paper>

                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.paper}> {`Contract Type :  ${row.contract}`}</Paper>

                            </Grid>
                        </Grid>
                        <Grid container item xs={12} spacing={3}>
                            <Grid item xs={4}>
                                <Paper className={classes.paper}> {`Location :  ${row.location}`} </Paper>

                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.paper}> {`Pay Scale :  ${row.payscall}`}</Paper>

                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.paper}> {`Work Shift :  ${row.work_shift}`}</Paper>

                            </Grid>
                        </Grid>
                    </Grid>
                </div>

                <Divider variant="middle" sx={{ mt: 2, mb: 3 }} />
                <Grid ontainer spacing={1}>
                    <Grid container item xs={12} spacing={3}>
                        <Grid item xs={4}>
                            <Paper className={classes.paper}>
                                Total Net Salary Paid <br /> RS : {`${row.basic_salery}`}
                            </Paper>
                        </Grid>
                        <Grid item xs={4}>
                            <Paper className={classes.paper}>
                                Total Net Salary Paid <br /> RS : 0
                            </Paper>
                        </Grid>
                        <Grid item xs={4}>
                            <Paper className={classes.paper}>
                                Total Net Salary Paid <br /> RS : 0
                            </Paper>
                        </Grid>

                    </Grid>

                </Grid>
                <div className={classes.PayRoll}>

                    <h4>
                        Detail of Staff
                    </h4>
                </div>

                <TableContainer component={Paper}>
                    <Table sx={{ minWidth: 650, marginTop: 5 }} aria-label="simple table">
                        <TableHead>
                            <TableRow className={classes.Trow}>
                                <TableCell>
                                    Pay slip #</TableCell>
                                <TableCell align="start">Month- year</TableCell>
                                <TableCell align="start">date</TableCell>
                                <TableCell align="start">mode</TableCell>
                                <TableCell align="start">status</TableCell>
                                <TableCell align="start">net salary (RS.)</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {paymentState.map((row) => (
                                <TableRow
                                    key={row.id}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {row.expenseHead}
                                    </TableCell>
                                    <TableCell align="start">{`${row.month}-${row.year}`}</TableCell>
                                    <TableCell align="start">{row.Day}</TableCell>
                                    <TableCell align="start">{row.cash}</TableCell>
                                    <TableCell align="start"><Label
                                       color= {row.amount ? "success" : "error"}
                                    >
                                        {row.amount ? "Paid" : "Pending"}

                                    </Label></TableCell>
                                    <TableCell align="start">{row.netSalery}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
        </>
    )
}

export default PayRoll