import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import {
  Avatar,
  CardMedia,
  Container,
  Divider,
  Grid,
  IconButton,
  List,
  ListItem,
  Popover,
  Tab,
  Table,
  TableCell,
  TableRow,
  Tabs,
} from "@material-ui/core";
import useAuth from "../../hooks/useAuth";
import { Box, Breadcrumbs, Button, Link } from "@material-ui/core";
import UploadIcon from "../../icons/Upload";
import DownloadIcon from "../../icons/Download";

import { useLocation } from "react-router-dom";
import ChevronRightIcon from "../../icons/ChevronRight";
import { Link as RouterLink } from "react-router-dom";
import { format } from "date-fns";
import { useRef } from "react";
import { useState } from "react";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import CloudDownloadIcon from "@material-ui/icons/CloudDownload";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import { position } from "stylis";

const useStyles = makeStyles({
  root: {
    minWidth: 275,
    height: "140vh",
  },
  table: {},

  title: {
    fontSize: 14,
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: 12,
  },
  pos: {
    marginBottom: 12,
  },
  padd: {
    // padding: "34px",
    paddingLeft: 30,
    paddingRight: 30,
    paddingTop: 10,
  },
  detail: {
    display: "flex",
  },
});
const Teacher_Detail = (props) => {
  const { row } = props;
  const classes = useStyles();
  const { user } = useAuth();
  const anchorRef = useRef(null);
  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const months = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "June",
    "July",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec",
  ];
  const d = new Date(row.dob);
  var name = months[d.getMonth()];

  var timeStr = row.dob;
  var date = new Date(timeStr);
  var day = date.getDate();
  var year = date.getFullYear();
  var month = name;
  var dateStr = day + "/" + month + "/" + year;

  var timeStr = row.admissiondate;
  var date = new Date(timeStr);
  var day = date.getDate();
  var year = date.getFullYear();
  var month = name;
  var dateStrr = day + "/" + month + "/" + year;
  return (
    <>
      <div className={classes.padd}>
        <Card className={classes.root}>
          <CardContent>
            <div className={classes.title}>
              <Typography variant="h5" gutterBottom>
                About Me
              </Typography>
              <IconButton color="inherit" ref={anchorRef} onClick={handleOpen}>
                <MoreHorizIcon />
              </IconButton>

              <Popover
                anchorEl={anchorRef.current}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "right",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                onClose={handleClose}
                open={open}
                PaperProps={{
                  sx: { width: 180 },
                }}
              >
                <List>
                  <ListItem>
                    <Button color="inherit">Download as CSV</Button>
                  </ListItem>
                  <ListItem>
                    <Button color="inherit">Download as PDF</Button>
                  </ListItem>
                </List>
              </Popover>
            </div>
            <div className={classes.detail}>
              <Container maxWidth="lg">
                <Box
                  sx={{
                    alignItems: "center",
                    display: "flex",
                    mt: 1,
                    position: "relative",
                  }}
                >
                  <CardMedia style={{ marginRight: "40px", display: "block" }}>
                    <Avatar
                      sx={{
                        border: (theme) =>
                          `4px solid ${theme.palette.background.default}`,
                        height: 120,
                        left: -19,
                        // position: "absolute",
                        // top: -60,
                        width: 120,
                      }}
                      alt={row.first_name}
                      src={`/uploads/${row.avatar}`}
                    />
                    {/* <img src={student} alt="student image" /> */}
                  </CardMedia>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      alignContent: "center",
                      flex: 1,
                    }}
                  >
                    <Typography variant="h5">{`${row.first_name}`}</Typography>

                    {/* <IconButton style={{ fontSize: "14px" }}>
                      <CloudDownloadIcon fontSize="small" />
                    </IconButton> */}
                  </div>
                </Box>
              </Container>
            </div>
            {/* <div>
              <Typography
                style={{
                  width: "50%",
                  marginLeft: "82px",
                  marginTop: "10px",
                }}
                variant="body2"
                component="p"
              >
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Ab,
                nostrum error eum commodi doloremque itaque vero obcaecati quia
                omnis fugit!
              </Typography>
            </div> */}
            <Container>
              <Box
                sx={{
                  position: "relative",
                  display: "flex",
                  alignItems: "center",
                }}
              >
                <TableContainer
                  style={{
                    marginTop: "20px",
                    position: "absolute",
                    left: 79,
                    top: -52,
                  }}
                >
                  <Table
                    className={classes.table}
                    aria-label="simple table"
                    style={{ marginLeft: "60px", width: "50%" }}
                  >
                    <TableHead></TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Father Name</TableCell>
                        <TableCell align="right">{row.father_name}</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Gender</TableCell>
                        <TableCell align="right">
                          {row.gender ? row.gender : "null"}
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Date Of Birth</TableCell>
                        <TableCell align="right">{dateStr}</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Date Of Joining</TableCell>
                        <TableCell align="right">
                          {row.date_of_joining}
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>E-mail</TableCell>
                        <TableCell align="right">{row.email}</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Address</TableCell>
                        <TableCell align="right">{row.location}</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Phone</TableCell>
                        <TableCell align="right">{`${row.phone}`}</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Qualification</TableCell>
                        <TableCell align="right">{`${row.qualification}`}</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableHead>
                      <TableRow>
                        <TableCell>Work Experience</TableCell>
                        <TableCell align="right">{`${row.work_experience}`}</TableCell>
                      </TableRow>
                    </TableHead>{" "}
                  </Table>
                </TableContainer>
              </Box>
            </Container>
          </CardContent>
        </Card>
      </div>
    </>
  );
};

export default Teacher_Detail;
