import React, { useEffect, useState } from "react";
import axios from "axios";
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  CircularProgress,
  Divider,
  Grid,
  Link,
  makeStyles,
  Paper,
  Typography,
} from "@material-ui/core";
import StudentAttenceCommon from "../StudentAttendenceCommon";
import customAxios from "../apis/Api";
import wait from "../../utils/wait";
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    // textAlign: "center",
    height: "120px",
    color: theme.palette.text.secondary,
  },
}));
const TeacherAttendence_ = ({ row, name, staffId }) => {
  const classes = useStyles();

  const [first, setfirst] = useState([]);
  const [loading,setLoading]=useState(false)
  const attendenceDetails = async () => {
    setLoading(true);
    await wait(2000)
    let date = new Date();
    let day = date.getDate();
    let month = date.getMonth() + 1;
    let year = date.getFullYear();

    // This arrangement can be altered based on how we want the date's format to appear.
    let currentDate = `${month}/${day}/${year}`;
    const data = await customAxios().get("attendence/total-staff-detail", {
      params: { startDate: row, endDate: currentDate, staffId: staffId },
    });
    setfirst(data.data);
    setLoading(false);
  };
  const obj = Object.assign({}, ...first);
  useEffect(() => {
    attendenceDetails();
  }, []);

  console.log(obj);

  return (
    <>
      <div style={{ paddingLeft: 16, paddingRight: 16, paddingTop: 10 }}>
        <Card>
          {loading ? (
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <CircularProgress />
            </Box>
          ) : (
            <>
              <CardHeader title={`Attendence Deatil Of ${name}`} />
              <Divider />
              <CardContent>
                <Grid container spacing={3}>
                  <Grid item xs={6} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                      <StudentAttenceCommon
                        first={obj.present}
                        text="Present"
                      />
                    </Paper>
                  </Grid>{" "}
                  <Grid item xs={6} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                      <StudentAttenceCommon first={obj.absent} text="Absent" />
                    </Paper>
                  </Grid>{" "}
                  <Grid item xs={6} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                      <StudentAttenceCommon first={obj.late} text="Late" />
                    </Paper>
                  </Grid>{" "}
                  <Grid item xs={6} sm={3}>
                    <Paper className={classes.paper} elevation={4}>
                      <StudentAttenceCommon first={obj.leave} text="Leave" />
                    </Paper>
                  </Grid>
                </Grid>

                <Box sx={{ mt: 2 }}></Box>
              </CardContent>
            </>
          )}
        </Card>
      </div>
    </>
  );
};

export default TeacherAttendence_;
