import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import {
  Avatar,
  CardMedia,
  Divider,
  Grid,
  IconButton,
  List,
  ListItem,
  Popover,
  Tab,
  Table,
  TableCell,
  TableRow,
  Tabs,
} from "@material-ui/core";
import useAuth from "../../hooks/useAuth";
import { Box, Breadcrumbs, Button, Link } from "@material-ui/core";
import UploadIcon from "../../icons/Upload";
import DownloadIcon from "../../icons/Download";

import { useLocation } from "react-router-dom";
import ChevronRightIcon from "../../icons/ChevronRight";
import { Link as RouterLink } from "react-router-dom";
import { format } from "date-fns";
import { useRef } from "react";
import { useState } from "react";
import Teacher_Detail from "./Teacher_Detail";
import PayRoll from "./PayRoll";
import TeacherAttendence_ from "./Teacher_Attendence";

const tabs = [
  { label: "Profile", value: "profile" },
  { label: "Pay Roll", value: "payroll" },
  { label: "Attendence", value: "attendence" },
];
const Student_Detail = () => {
  const { state } = useLocation();
  const { row } = state;
 console.log(row)
  const [currentTab, setCurrentTab] = useState("profile");

  const changeTab = (event, value) => {
    setCurrentTab(value);
  };

  return (
    <>
      <Grid
        style={{ paddingTop: "30px", paddingLeft: "30px" }}
        container
        justifyContent="space-between"
        spacing={3}
      >
        <Grid item>
          <Typography color="textPrimary" variant="h5">
            Staff Detail
          </Typography>
          <Breadcrumbs
            aria-label="breadcrumb"
            separator={<ChevronRightIcon fontSize="small" />}
            sx={{ mt: 1 }}
          >
            <Link
              color="textPrimary"
              component={RouterLink}
              to="/dashboard/maindash"
              variant="subtitle2"
            >
              Dashboard
            </Link>
            <Link
              color="textPrimary"
              component={RouterLink}
              to="/dashboard/teachers"
              variant="subtitle2"
            >
              Teacher List
            </Link>
            <Typography color="textSecondary" variant="subtitle2">
              Teacher Detail
            </Typography>
          </Breadcrumbs>
        </Grid>
      </Grid>
      <Box sx={{ mt: 3, marginLeft: "35px" }}>
        <Tabs
          indicatorColor="primary"
          onChange={changeTab}
          scrollButtons="auto"
          textColor="primary"
          value={currentTab}
          variant="scrollable"
        >
          {tabs.map((tab) => (
            <Tab key={tab.value} label={tab.label} value={tab.value} />
          ))}
        </Tabs>
      </Box>
      <Divider />
      <Box>
        {currentTab === "profile" && <Teacher_Detail row={row} />}
        {currentTab === "payroll" && <PayRoll row={row} />}
        {currentTab === "attendence" && (
          <TeacherAttendence_
            row={row.date_of_joining}
            name={row.first_name}
            staffId={row.staff_id}
          />
        )}
      </Box>
    </>
  );
};

export default Student_Detail;
